/* The purpose of this file is to reset all CSS properties that the browser may otherwise want to reset. Taken directly from the webpage cited below.*/
/*
	http://meyerweb.com/eric/tools/css/reset/ 
	v2.0 | 20110126
	License: none (public domain)
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

/* Navbar color---------------------------------------------- */
.navbar {
  background: #00167a;
  background: -webkit-linear-gradient(#003180, #00167a);
  background: -o-linear-gradient(#003180, #00167a);
  background: -moz-linear-gradient(#003180, #00167a);
  background: linear-gradient(#003180, #00167a);
}

.show {
  display: block;
}

::-webkit-input-placeholder :-moz-placeholder ::-moz-placeholder :-ms-input-placeholder {
  color: white;
}

.logoFull {
  display: block;
  margin: auto;
  padding: 10px;
  width: 55%;
}

.navbar .navbar ul {
  text-align: center;
  list-style-type: none;
  padding-left: 0;
  width: 100%;
  display: -webkit-flex;
  justify-content: center;
  display: flex;
  flex-direction: row;
}

.navbar .navbar ul li {
  flex-grow: 2;
  flex-shrink: 0.7;
  margin: 0;
  padding: 0.2em;
}

.navbar .navbar ul li a {
  font-weight: bolder;
  color: white;
  display: block;
  text-decoration: none;
  text-align: center;
  font-size: 16px;
  padding: 0.35em;
  white-space: nowrap;
  border-radius: 0.1em;
}

.navbar .navbar ul li a:hover {
  background-color: #0044b3;
  color: black;
  transition: all 0.15s;
}

.navbar .navbar ul #search {
  flex-grow: 30;
  flex-shrink: 1;
}

.navbar .navbar ul #search #search_form {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}

.navbar .navbar ul #search #search_form #search_text {
  text-align: center;
  border-color: transparent;
  color: white;
  outline: none;
  background: #C8C8FF;
  float: center;
  transition: all 0.15s;
  border-radius: 0.1em;
  width: 100%;
  resize: none;
  font-size: inherit;
  font-family: inherit;
}

.navbar .navbar ul #search #search_form #search_text:focus {
  color: black;
  background: white;
}

.navbar .navbar ul #logIn {
  flex-grow: 1;
  flex-shrink: 0.5;
  position: relative;
  display: inline-block;
}

.navbar .navbar ul #logIn #logInButton {
  background-color: #0044b3;
}

/* LOGIN DROPDOWN */
#logInHidden {
  padding: 5px;
  animation-duration: 2s;
  text-align: right;
  display: none;
  position: absolute;
  background-color: #f9f9f9;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  z-index: 1;
  right: 0;
}

#logInHidden .linkDefault {
  color: blue;
  padding-top: 2px;
  padding-bottom: 2px;
  text-decoration: none;
  border-radius: 0;
  background: none;
  font-size: 12px;
  font-weight: normal;
  text-align: right;
}

#logInHidden .linkDefault:hover {
  color: red;
  background: none;
}

#logInHidden #logInUser, #logInHidden #logInPwd {
  text-align: center;
  border-color: transparent;
  color: white;
  outline: none;
  background: #C8C8FF;
  float: center;
  transition: all 0.15s;
  border-radius: 0.1em;
  resize: none;
  font-size: inherit;
  font-family: inherit;
}

#logInHidden #logInSub {
  text-align: right;
  border: none;
  color: white;
  text-decoration: none;
  text-align: center;
  background: blue;
}

html {
  font-family: 'Open Sans', sans-serif;
}

#pageWidth {
  width: 80%;
  margin: auto;
  max-width: 1200px;
}

#main {
  background-color: rgba(133, 133, 133, 0.3);
  font-size: 16px;
}

@media (max-width: 900px) {
  #pageWidth {
    width: 720px;
  }
}
